<template>
	<div class="confirmOrder">
		<header >
			<i class='iconfont icon-back' @click='$router.go(-1)'></i>
			<span>确认订单</span> 
		</header>
		<div class="place"></div>
		<div class="userInfo">
			<div class="left">
				<img src="https://gw.alicdn.com/tfs/TB17gX2wYvpK1RjSZPiXXbmwXXa-128-128.png_70x70q90_.webp">
			</div>
			<div class="right">
				<p class='name'>
					某某先生 
					<span class='phone'>12345678910</span>
				</p>
				<p class='address'>
					浙江省温州市乐清市瑞安市龙湾区鹿城区瓯海区 某某街道
					<i class="iconfont icon-right"></i>
				</p>
				<p>收货不便时,可选择免费暂存服务</p>
			</div>
		</div>
		<div class="number">
			<van-divider>共{{goodsList.length}}件商品</van-divider>
		</div>
		<div class="main">
			<van-card
			v-for='(val,index) in goodsList'
			  :num="val.number"
			  :price="val.price"
			  :desc="val.spec"  
			  :title="val.name"
			  :thumb="val.img"
			  :key='index'/>
		</div>
		<div class="info">
			<p>商品总价：<span>¥{{filterTotal}}</span></p>
			<p>运费：<span>¥{{freight}}</span></p>
		</div>
		<van-submit-bar
		  :price="total"
		  button-text="提交订单"
		  @submit='order'
		>
		</van-submit-bar>
		<van-popup
		  	v-model="showPop"
		  	position="bottom"
		  	:closeable=true
		  	:close-on-click-overlay='false'
		  	close-icon-position='top-left'
		  	:style="{ height: '66%' }"
		  	:duration=0.17
		  	@closed='closePop'
		>
			<div class="title">
				{{showKeyboard?'请输入支付密码':'确认付款'}}
				<img src="https://s2.ax1x.com/2019/11/21/M5E7od.png" alt="" v-show='!showKeyboard'>
			</div>
			<div class='popMain' v-show='!showKeyboard' >
				<div class="lastPrice">
					¥ <span>{{(total/100).toFixed(2)}}</span>
				</div>
				<div class="list" >
					<p>
						<span class="itemTitle">支付宝账号</span>
						<span>123******89</span>
					</p>
					<p>
						<span class="itemTitle">付款方式</span>
						<span>账户余额 <i class='iconfont icon-right'></i></span>
					</p>
				</div>
			</div>	
			<div class="enterPass" v-show='showKeyboard'>
				<!-- 密码输入框 -->
				<van-password-input
				  :value="password"
				  info="忘记密码？找回并完成支付"
				  :focused="false"
				  @focus="showKeyboard = true"
				/>

				<!-- 数字键盘 -->
				<van-number-keyboard
				  :show="showKeyboard"
				  @input="onInput"
				  @delete="onDelete"
				/>
			</div>
			<div class="bottom">
				<img src="https://s2.ax1x.com/2019/11/21/M5eqY9.png" alt="">
				请验证指纹或者 <span @click='enterPass'>输入密码</span>
			</div>
		</van-popup>
	</div>
</template>
<script type="text/javascript">
	export default{
		data(){
			return{
				goodsList:[],
				total:0,
				showPop:false,
				freight:12,
				password: '',
      			showKeyboard: false
			}
		},
		methods:{
			order(){
				this.showPop=true;
				this.password='';
			},
			enterPass(){
				this.showKeyboard=true;
				let del=document.querySelector('.van-key--delete');
				del.innerHTML='<img src="https://s2.ax1x.com/2019/11/21/M5HMVg.png">';
			},
			onInput(key) {
		      this.password = (this.password + key).slice(0, 6);
		      if(this.password.length==6){
		      	this.$toast({
		      	 	type:'success',
		      	 	message:'密码输入完成'
		      	});
		      	this.showPop=false;
		      }
		    },
		    onDelete() {
		      	this.password = this.password.substring(0, this.password.length - 1);
		    },
		    closePop(){
		    	this.showKeyboard=false;
		    }
		},
		computed:{
			filterTotal(){
				return this.total/100 - this.freight;
			}
		},
		// computed是计算属性，如果一个数据依赖其他数据，使用计算属性
		// watch是监听器，当指定的data数据发生变化时执行异步操作(访问一个 API)或开销大的操作(就是需要在某个data数据变化时做一些事情)，使用监听器。
		// 计算属性会进行缓存，只在相关响应式依赖发生改变时它们才会重新求值。
		// watch:{
		// 		total(newValue){
		// 			this.filterTotal=newValue/100-this.freight;
		// 		}
		// },
		activated(){
			this.total=0;
			let list=JSON.parse(localStorage.getItem('confirmOrder'));
			list.map(val=>{
				val.spec='规格 '+val.spec;
				this.total+=val.price*val.number*100;
			})
			this.total+=this.freight*100;
			this.goodsList=list;
		},
		deactivated(){
			this.goodsList=[];
			localStorage.removeItem('confirmOrder');
		},
		beforeRouteLeave(to,from,next){
			if(this.showPop){
				this.showPop=false;
				this.$router.go(1);
				return;
			}
			else{
				next();
			}
		}
	}
</script>
<style lang='stylus' scoped>
.confirmOrder
	background:#f2f2f2;
	overflow: auto;
	height:100%;
	header
		position:fixed;
		top:0;
		width:92%;
		padding:0 4%;
		height:1rem;
		font-size:.4rem;
		display:flex;
		align-items:center;
		background:#fff;
		z-index:2;
		transition:.2s;
		.iconfont
			font-size:.48rem;
		span
			position:absolute;
			left:50vw;
			transform:translateX(-50%);
			font-weight:700;
	.place
		height:1rem;
	.userInfo
		display:flex;
		align-items:center;
		width:90vw;
		border-radius:.2rem;
		box-sizing:border-box;
		padding: .2rem  3%;
		background:#fff;
		margin:.2rem auto;
		margin-top:.38rem;
		.left
			img
				width: .7rem;
				height:.7rem;
		.right
			display:flex;
			margin-left:.2rem;
			flex-direction:column;
			position:relative;
			p
				margin: 0;
				padding:.08rem 0;
			.name
				font-size:.32rem;
				.phone
					color:rgb(153,153,153);
					font-size:.26rem;
			.address
				font-size:.26rem;
				width: 87%;
				.iconfont
					position:absolute;
					right:.1rem;
					top:50%;
					transform:translateY(-50%);
					color:#ccc;
			p:last-child
				font-size:.26rem;
				color:rgb(225,80,0);
			
	.number
		width:90vw;
		margin:0 auto;
		.van-divider
			color:#000;
			font-size:.3rem;
			margin:.38rem auto;
			font-weight:500;
		.van-divider--hairline::after, .van-divider--hairline::before
			transform: scaleY(1);
			border-color:darkgrey;		
	.main
		margin-bottom:.5rem;
		.van-card
			width: 90%;
			padding: .16rem  3%;
			margin:.4rem auto;
			box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
			border-radius: 10px;
			background:#fff;
			.van-card__title
				font-size:.3rem;
				width:94%;
				letter-spacing:1px;
				display: -webkit-box;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 2;
				overflow: hidden;
				line-height: inherit;
				max-height: inherit;
			.van-card__price
				font-size:.3rem;
			.van-card__num
				font-size:.28rem;
			.van-card__desc
				margin:.02rem 0 .1rem 0;
			.van-card__bottom
				margin-right:.16rem;
		.van-card >>> img.van-image__img
			object-fit: fill !important;
			border-radius:.2rem;
	.info
		width: 90%;
		padding: .16rem  3%;
		margin:.4rem auto;
		box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
		border-radius: 10px;
		font-size:.34rem;
		box-sizing:border-box;
		margin-bottom:1.8rem;
		background:#fff;
		p
			margin:.2rem;
			display:flex;
			align-items:center;
			justify-content:space-between;
	.van-popup--bottom
		.title
			margin-top:.3rem;
			font-size:.36rem;
			display:flex;
			align-items:center;
			justify-content:center;
			position:relative;
			padding-bottom:.28rem;
			border-bottom:1px solid #eee;
			img
				width:.48rem;
				height:.48rem;
				position:absolute;
				right:.3rem;
		>>>.van-popup__close-icon
			font-size:.5rem;
		.popMain
			.lastPrice
				text-align:center;
				font-size:.4rem;
				margin-top:.76rem;
				font-weight:700;
				span
					font-size:.88rem;
					font-family: arial;
					margin-left:.1rem;
			.list
				margin-top:.2rem;
				p:first-child
					border-bottom:1px solid #eee;
				p
					display:flex;
					align-items:center;
					justify-content:space-between;
					font-size:.32rem;
					padding:.22rem 4%;
					margin:.1rem 0;
					color:#000;
					.itemTitle
						color:#9e9e9e;
					span
						.icon-right
							color:#9e9e9e;
		.bottom
			background:#efeef4;
			height:17%;
			position:absolute;
			bottom:0;
			left:0;
			width: 100%;
			display:flex;
			align-items:center;
			justify-content:center;
			font-size:.34rem;
			color:#7e7d83;
			img
				width: .64rem;
				height:.64rem;
				margin-right:.2rem;
			span
				margin-left:.2rem;
				color:#3682bc;	
		.enterPass
			>>>[class*=van-hairline]:after
				border-color:#e1e1e1;
			>>>.van-number-keyboard__body
				font-size:0;
			>>>.van-key:after
				border-width:.03rem .03rem 0 0;
				border-color:#ddd  !important;
			.van-password-input__info
				margin-top:.3rem;
			ul.van-password-input__security
				margin-top:.38rem;
				height:1rem;
			.van-password-input__security::after
				border-radius:.2rem;
				border:.05rem solid #ddd;
			.van-hairline--left::after
				border-left-width:.045rem;
			.van-password-input__info
				color:#378fd4;
				font-size:.3rem;
			>>>.van-key
				font-size:.56rem;	
				height:1.1rem;
				line-height:1.1rem;
			>>>.van-key--delete
				position:relative;
				img
					width: 27%;
					max-height:42%;
					position:absolute;
					left:50%;
					top:50%;
					transform: translate(-50%,-50%);
</style>


